import React from 'react';
import { Link, NavLink } from 'react-router-dom';

const NavBar = () => {
    return ( 
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
            <Link className="navbar-brand" to="/">Zokosani</Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                {/* <NavLink className="nav-item nav-link" to="/users">Users</NavLink> */}
                <NavLink className="nav-item nav-link" to="/policies">Policy</NavLink>
                <NavLink className="nav-item nav-link" to="/district">District</NavLink>
                <NavLink className="nav-item nav-link" to="/members">Member</NavLink>
                </div>
            </div>
        </nav>
     );
}
 
export default NavBar;