import { Route, Redirect, Switch } from 'react-router-dom';
import LoginForm from './components/login';
import RegisterForm from './components/register';
import Users from './components/users/users';
import UserForm from './components/users/userForm';
import Policy from './components/policy/policy';
import PolicyForm from './components/policy/policyForm';
import District from './components/district/district';
import DistrictForm from './components/district/districtForm';
import Member from './components/member/member';
import MemberForm from './components/member/memberForm';
import NotFound from './components/notFound';
import NavBar from './components/navBar';
import './App.css';

function App() {
  return (
    <>
      <NavBar />
      <main className='container'>
        <Switch>
          <Route path="/login" component={LoginForm} />
          <Route path="/register" component={RegisterForm} />
          <Route path="/users" component={Users}/>
          <Route path="/users/:id" component={UserForm} />
          <Route path="/district" component={District}></Route>
          <Route path="/district/:id" component={DistrictForm}></Route>
          <Route path="/policies" component={Policy}></Route>
          <Route path="/policies/:id" component={PolicyForm}></Route>
          <Route path="/members" component={Member} />
          <Route path="/members/:id" component={MemberForm} />
          <Route path="/not-found" component={NotFound} />          
          <Redirect from='/' exact to='/members' />
          <Redirect to='/not-found' />
        </Switch>
      </main>
    </>
  );
}

export default App;
