import http from './httpService';
import config from '../config.json';

const apiEndPoint = config.apiUrl + '/district';
export function getDistricts(){
    return http.get(apiEndPoint);
}

export function getDistrict(districtId){
    return http.get(apiEndPoint + '/' + districtId)
}

export function updateDistrict(district){
    if(district._id){
        const body = {...district}
        return http.put(apiEndPoint + '/' + district._id, body)
    }
}

export function deleteDistrict(districtId){
    return http.delete(apiEndPoint + '/' + districtId);
}